/**
 * 最外层包裹的home组件，包括
 */
import React, { PureComponent } from 'react';
import BasicLayout from 'Layouts/BasicManage';
import {
  Route, Redirect,
} from 'react-router-dom';
import {
  DefinePage,
  ConfigPage,
  MessagePage,
  MaintainPage,
  TitleConfigPage
} from './configs'

class BasicRouter extends PureComponent {
  render() {
    return <BasicLayout children={<React.Fragment>
      <Route exact path="/basic" render={() => <Redirect to='/basic/device/define' />} />
      <Route path='/basic/device/define' component={DefinePage} />
      <Route path='/basic/device/configuration' component={ConfigPage} />
      <Route path='/basic/message' component={MessagePage} />
      <Route path='/basic/maintain' component={MaintainPage} />
      <Route path='/basic/titleconfig' component={TitleConfigPage} />
    </React.Fragment>} />;
  }

}
export default BasicRouter;